<template>
  <div class="container-fluid">
    <div class="row mt-3">
      <div class="col-3">
        <div class="card">
          <div class="card-header">
            Tasks
          </div>
          <ul class="list-group list-group-flush">
            <!-- Tasks Headers here -->
            <li class="list-group-item pointer" v-for="task in tasks" :key="task.id" @click="set_task_by_id(task.id)">{{task.id}}</li>
          </ul>
        </div>
      </div>
      <div class="col-9">
        <!-- Tasks cards Here -->
        <div class="container">
          <TaskCard :is="selected_task" :id="selected_task.id" :struct="selected_task.struct" :active="selected_task.active"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TaskCard from './components/TaskCard.vue';

export default {
  name: 'App',
  components: {
    TaskCard
  },
  data() {
    return {
      "tasks": [{
        "id": "test",
        "struct":
          [{ "key": "test_progress", "component": "ProgressElement", "data": { "current": 50, "max": 300 } },
          { "key": "test_plot", "component": "ChartElement", "labels": { "label_x": "", "label_y": "" }, "data": { "x" : [1,2,3,4,5], "y" : [2,7,4,9,2]} },
          { "key": "test_log", "component": "LogElement", "data": ['hi', 'this', 'is', 'a', 'test'] }],
        "active": true
      },
      {
        "id": "test2",
        "struct":
          [{ "key": "test_progress", "component": "ProgressElement", "data": { "current": 200, "max": 300 } },
          { "key": "test_plot", "component": "ChartElement", "labels": { "label_x": "", "label_y": "" }, "data": { "x" : [1,2,3,4,5], "y" : [2,3,4,5,2]}},
          { "key": "test_log", "component": "LogElement", "data": [] }],
        "active": true
      }],
      selected_task_index: 1
    };
  },
  computed: {
    selected_task() {
      return this.tasks[this.selected_task_index]
    }
  },
  methods: {
    set_task_by_id(target_id) {
      this.selected_task_index = this.tasks.findIndex(x => x.id === target_id);
    }
  }
};

</script>

<style>
li.pointer {
  cursor: pointer
}
</style>
