<template>
    <div class="row mb-3">
        <div class="col">
            <h5> {{ id }}</h5>
            <Line :chart-options="chartOptions" :chart-data="chartData" />
        </div>
    </div>
</template>

<script>
import { Line } from 'vue-chartjs';
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale,
} from 'chart.js'

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale
)

export default {
    name: 'ChartElement',
    props: {
        id: { type: String },
        data: { type: Object }
    },
    components: { Line },
    data() {
        return {
            chartData: {
                labels: this.data.x,
                datasets: [
                    {
                    label: "test",
                    backgroundColor: '#f87979',
                    data: this.data.y
                    }
                ]
            },
            chartOptions: {
                responsive: true,
                maintainAspectRatio: false
            }
        }
    }
};
</script>

